import React, { FC, useEffect, useState } from 'react';
import { OpacityOptions } from '../types';
import First from './layers/first';
import Main from './layers/main';
import Second from './layers/second';
import Third from './layers/third';
import styles from './pizza.module.scss'
import cn from 'classnames';
import { useAppSelector } from '../../../store/hooks';
import { getIsPlayed, getIsSelected } from '../../../store/game/selectors';

const Pizza: FC<any> = () => {
  const isPlayed = useAppSelector(getIsPlayed);
  const isSelected = useAppSelector(getIsSelected);
  const [layerOpacities, setLayerOpacities] = useState<number[]>([0, 0, 0, 0]);

  const setOpacity = ({ index, isReverse }: OpacityOptions): number => {
    setLayerOpacities(prevOpacities => {
      const newOpacities = [...prevOpacities];
      newOpacities[index] = isReverse ? 1 : 0;
      return newOpacities;
    });
    isReverse ? index++ : index--;
    return index;
  }

  useEffect(() => {
    if (isPlayed) {
      let i = 0;
      const intervalId = setInterval(() => {
        if (i === 4) {
          clearInterval(intervalId);
        }
        const options: OpacityOptions = { index: i, isReverse: true };
        i = setOpacity(options);
      }, 300);

      return () => clearInterval(intervalId);
    }
  }, [isPlayed]);

  useEffect(() => {
    if(isSelected) {
      setLayerOpacities(prevOpacities => prevOpacities.map(item => item = 1));
      let i = 4;
      const intervalId = setInterval(() => {
        if (i === 1) {
          setLayerOpacities(prevOpacities => prevOpacities.map((item, i) => {
            if(i !== 0) {
              return 0;
            } else {
              return item;
            }
          }));
          clearInterval(intervalId);
        }
        i = setOpacity({ index: i });
      }, 300);

      return () => clearInterval(intervalId);
    }
  }, [isSelected]);

  return (
    <svg 
      xmlns="http://www.w3.org/2000/svg" 
      width="128"
      height="128"
      fill="none"
      className={cn(styles.pizzaSvg)}>
        <Main opacity={layerOpacities[0]} />
        <First opacity={layerOpacities[1]} />
        <Second opacity={layerOpacities[2]} />
        <Third opacity={layerOpacities[3]} />
    </svg>
  );
};

export default Pizza;
